.scroller {
    width: 100%;
    height: 160px;
    bottom: 0;
    margin-top: 2px;
    margin-bottom: 2px;
    border: none;
    border-radius: 12px;
    font-size: 24px;
    background-color: #c9d3db;
    overflow-x: scroll;
    scroll-behavior: smooth;
    display: flex;
    cursor: grab;
    padding-inline-start: 0px;
}

.scroller-content {
    margin: 5px;
    display: inline-block;
    width: 110px;
    height: 110px;
    border: 1px solid #000000;
    border-radius: 5px;
    background-color: darkslategray;
    color: #b4d1d1;
    cursor: pointer;
}

::-webkit-scrollbar {
    width: 0px;
    height: 0px;
    background: transparent;
}